.tool {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 25px 10px 10px;
  padding-left: 20px;
}

.palette, .canvas-size {
  margin-left: 15px;
  margin-bottom: 20px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  border: 1px solid #d4d4d4;
  box-shadow: 0px 5px 7px -1px rgba(136, 135, 135, 0.77);
  color: #7e7c7c;
}

.palette>div {
  display: flex;
  justify-content: space-between;
}

.palette>div>div {
  width: 40px;
  height: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  font-size: 25px;
}

.palette>div>div:hover::after {
  content: attr(data-title);
  position: absolute;
  left: 115%;
  top: 0%;
  z-index: 1;
  background: rgba(255, 230, 249, 0.9);
  font-family: Arial, sans-serif;
  font-size: 10px;
  padding: 2px 4px;
  border: 1px solid rgb(78, 2, 68);
  border-radius: 3px;
}

.palette>div>div:hover, .colors-part li:hover {
  cursor: pointer;
  color: #ad4646;
  font-weight: 600;
}

.tool .canvas-size p {
  width: 100px;
  border: 1px solid #d4d4d4;
  text-align: center;
}

.tool .canvas-size p:hover {
  cursor: pointer;
  border: 1px solid #f742ee;
  background-color: #f5e3f5;
}

.colors-part {
  position: relative;
  width: 90px;
  height: 90px;
  margin-left: 15px;
  margin-bottom: 20px;
}

.colors-part .primary_color {
  position: absolute;
  width: 40px;
  height: 40px;
  border: 2px solid #7a7a7a;
  z-index: 1000;
}

.colors-part .secondary_color {
  position: absolute;
  width: 40px;
  height: 40px;
  border: 2px solid #7a7a7a;
  z-index: 1000;
  position: relative;
  left: 20px;
  top: 20px;
}

.colors-part>div:hover::after {
  content: attr(data-title);
  position: absolute;
  left: 115%;
  top: 0%;
  z-index: -1;
  background: rgba(255, 230, 249, 0.9);
  font-family: Arial, sans-serif;
  font-size: 10px;
  padding: 2px 4px;
  border: 1px solid rgb(78, 2, 68);
  border-radius: 3px;
}

.colors-part button {
  position: relative;
  left: 355px;
  top: -50px;
  z-index: 5;
}
