.frame {
  width: 130px;
  margin-bottom: 8px;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  border: 2px solid #acacac;
  box-shadow: 0px 5px 7px -1px rgba(136, 135, 135, 0.77);
  color: #7e7c7c;
  margin-top: -59px;
}

.frame-area {
  width: 128px;
  height: 128px;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  border: 1px solid #969595;
  align-items: stretch;
  font-size: 15px;
}
