.preview {
  width: 25%;
  height: 300px;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  margin-top: 25px;
  border: 1px solid #d4d4d4;
  box-shadow: 0px 5px 7px -1px rgba(136, 135, 135, 0.77);
  align-items: center;
}

#tickmarks {
  display: flex;
  justify-content: space-between;
  font-size: 0.6em;
}

.preview .frame {
  width: 132px;
  margin-bottom: 8px;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  border: 2px solid #d4d4d4;
  box-shadow: 0px 5px 7px -1px rgba(136, 135, 135, 0.77);
  color: #7e7c7c;
  margin-top: 3px;
}

.preview canvas {
  width: 128px;
  height: 128px;
}

.preview button {
  padding: 5px;
  margin-top: 10px;
  border: 2px inset rgb(165, 163, 163);
}

.preview button:focus {
  outline: none;
}

/* Chrome, Safari and Opera syntax */

:-webkit-full-screen {
  background-color: #e5e2e2;
}

/* Firefox syntax */

:-moz-full-screen {
  background-color: #e5e2e2;
}

/* IE/Edge syntax */

:-ms-fullscreen {
  background-color: #e5e2e2;
}

/* Standard syntax */

:fullscreen {
  background-color: #e5e2e2;
}

:-webkit-full-screen .frame {
  width: 512px;
  height: 512px;
  margin-bottom: 8px;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  border: 2px solid #d4d4d4;
  box-shadow: 0px 5px 7px -1px rgba(136, 135, 135, 0.77);
  color: #7e7c7c;
  margin-top: 3px;
}

:-webkit-full-screen .frame-area {
  width: 512px;
  height: 512px;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  border: 1px solid #808080;
  align-items: stretch;
  font-size: 15px;
}

:-webkit-full-screen canvas {
  width: 512px;
  height: 512px;
}
