.frames {
  width: 17%;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  margin-top: 25px;
  align-items: center;
}

.frames_button {
  height: 45px;
  width: 130px;
  display: flex;
  justify-content: space-around;
  align-items: center;
  border: 4px solid #b9b8b8;
  box-shadow: 0px 5px 7px -1px rgba(136, 135, 135, 0.77);
}

.frames_button:focus {
  outline: none;
}

.frames_button i {
  color: #7e7c7c;
  font-size: 25px;
}

.frames_button p {
  display: flex;
  width: 50%;
  color: #000000;
  font-size: 12px;
}

.frame_delete {
  width: 30px;
  height: 30px;
  color: rgb(131, 125, 125);
  position: relative;
  left: 100px;
  top: -24px;
  font-size: 25px;
}

.frame_delete:hover {
  cursor: pointer;
  color: rgb(103, 21, 180);
}

.frame_number {
  width: 30px;
  height: 30px;
  color: rgb(131, 125, 125);
  position: relative;
  left: 15px;
  top: 4px;
  font-size: 25px;
}

.frame_duplicate {
  width: 30px;
  height: 30px;
  color: rgb(131, 125, 125);
  position: relative;
  left: 100px;
  bottom: 48px;
  font-size: 25px;
}

.frame_duplicate:hover {
  cursor: pointer;
  color: rgb(103, 21, 180);
}

.frames canvas {
  width: 128px;
  height: 128px;
}

.frame_drag {
  width: 30px;
  height: 30px;
  color: rgb(131, 125, 125);
  position: relative;
  left: 4px;
  bottom: 78px;
  font-size: 25px;
}

.frame_drag:hover {
  cursor: pointer;
  color: rgb(103, 21, 180);
}
