.landing {
  width: 100%;
  min-height: 100vh;
  background: linear-gradient(180deg, rgba(255, 255, 255, 0.9) 15%, rgba(255, 255, 255, 0.7), rgba(255, 255, 255, 0.7), rgba(255, 255, 255, 0.8), rgba(255, 255, 255, 0.9)), url(../../image/back1.png) 100% 100% repeat repeat;
  align-items: center;
  text-align: center;
}

.landing header {
  position: fixed;
  font-size: 20px;
  font-family: 'Courier New', Courier, monospace;
  font-weight: 900;
}

.landing main {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.screenshots {
  display: flex;
  flex-direction: column;
}

.screenshots>div img {
  width: 500px;
  height: 220px;
  border: 2px dotted rgb(116, 113, 113);
  border-radius: 6px;
  margin: 60px 15px 10px;
}

.landing footer {
  display: flex;
  justify-content: space-around;
  align-items: center;
  background-color: rgba(128, 117, 138, 0.664);
  font-family: 'Courier New', Courier, monospace;
  font-size: 18px;
}

.overview {
  width: 80%;
  display: flex;
  flex-direction: column;
}

.overview h2 {
  font-size: 40px;
  font-family: 'Courier New', Courier, monospace;
  font-weight: 900;
  color: rgb(88, 16, 16);
}

.overview>div {
  width: 60%;
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
  margin: 15px;
  font-size: 25px;
  font-weight: 600;
  font-family: 'Courier New', Courier, monospace;
  color: rgb(87, 87, 87);
}

.overview>div>i {
  margin: 7px 10px;
  font-size: 25px;
  color: rgb(88, 16, 16);
}

.overview>div:nth-child(5)>i {
  margin-top: 4px;
  margin-right: 16px;
  font-size: 27px;
}
