* {
  margin: 0px;
  padding: 0px;
}

.App {
  width: 100%;
  min-height: 100vh;
  background: url(../src/image/square_bg.png) 100% 100% repeat repeat;
  align-items: center;
  text-align: center;
}

li {
  list-style-type: none;
}

header {
  width: 100%;
  height: 48px;
  display: flex;
  align-items: center;
  justify-content: center;
  box-shadow: 0px 3px 5px -1px rgba(136, 135, 135, 0.77);
  background-color: #e0dbdb;
  letter-spacing: 2px;
  font-family: Arial, Helvetica, sans-serif;
  color: #888787;
}

.fa-bars {
  margin: auto 2%;
  font-size: 23px;
  font-weight: 600;
  color: #888787;
}

.fa-ellipsis-v {
  position: absolute;
  right: 2%;
  font-size: 23px;
  font-weight: 600;
  color: rgb(136, 135, 135);
}

main {
  width: 100%;
  display: flex;
  justify-content: space-around;
}

.button_editor {
  position: fixed;
  width: 250px;
  height: 30px;
  top: 5px;
  right: 10px;
  border: 1px dotted rgb(90, 89, 89);
  border-radius: 6px;
  box-shadow: 0px 3px 5px -1px rgba(136, 135, 135, 0.77);
  font-family: 'Courier New', Courier, monospace;
  font-size: 20px;
  font-weight: 900;
  letter-spacing: 5px;
  color: rgb(104, 31, 105);
  background-color: rgb(54, 196, 196);
}

.frame_border {
  width: 135px;
  height: 140px;
  margin: 10px 0px;
  border: 3px solid #ffd700;
}

.frame_border-none {
  width: 135px;
  height: 140px;
  margin: 10px 0px;
  border: 3px solid #e2e0e0;
}
